<template>
  <div class="banner">
    <img class="pic" src="@/assets/img/banner.jpg" alt="banner" v-if="types==1" />
    <div v-else>
      <MxVideo />
    </div>
  </div>
</template>

<script>
import MxVideo from '@/components/basic/video'
export default {
  name: 'banner',
  components: {
    MxVideo
  },
  props: {
    types: {
      type: [Number, String],
      default: 1
    }
  }
}
</script>

<style lang='less' scoped>
.banner {
  width: 100vw;
  display: block;
  // height: 47vw;
}
.pic {
  width: 100%;
  display: block;
}
</style>



