<template>
  <layout>
    <div slot="top" class="top">
      <ls-banner :types="types"></ls-banner>
    </div>
    <div slot="content" class="content">
      <router-view />
      <div class="gameWorksBottomBtn" v-if="isShowGameWorksBtn">
        <div class="progress btn" :class="{'change': gameWorks}" @click="toGameWorks">大赛进程</div>
        <div class="introduce btn" :class="{'change': gameIntroduce}" @click="toGameIntroduce">大赛介绍</div>
      </div>
    </div>
  </layout>
</template>
<script>
import lsBanner from '../../components/basic/banner'
import layout from '../../components/layout/layout'
import { mapState } from 'vuex'
import Http from '../../api'
export default {
  components: {
    layout,
    lsBanner
  },
  data () {
    return {
      types: 1,
      gameIntroduce: false,
      gameWorks: false
    }
  },
  watch: {
    $route: {
      async handler (to, from) {
        if (to.path === '/option/gameIntroduce') {
          this.types = 2
          if (!this.videoInfo.name) {
            await this.$store.dispatch('admin/gameIntroduction/getVideoInfo')
          }
        } else {
          this.types = 1
        }
      },
      deep: false,
      immediate: true
    }
  },
  async created () {
    const { juryList } = Http.gameIntroduction
    const a = await juryList()
    console.log(a, 'a')
    const path = this.$route.path
    this.gameIntroduce = path === '/option/gameIntroduce'
    this.gameWorks = path === '/option/gameWorks'
  },
  async mounted () {},
  computed: {
    ...mapState({
      videoInfo: state => state.admin.gameIntroduction.videoInfo
    }),
    isShowGameWorksBtn () {
      const path = this.$route.path
      const judge =
        path === '/option/gameWorks' || path === '/option/gameIntroduce'
      return judge
    }
  },
  methods: {
    toGameWorks () {
      this.gameWorks = true
      this.gameIntroduce = false
      this.$router.push({ path: '/option/gameWorks' })
    },
    toGameIntroduce () {
      this.gameWorks = false
      this.gameIntroduce = true
      this.$router.push({ path: '/option/gameIntroduce' })
    }
  }
}
</script>
<style lang="less">
.top {
  width: 100%;
  // height: 22vh;
  .top-banner {
    width: 100%;
    height: 100%;
  }
}
.content {
  // min-height: 78vh;
  // background: #f4f4f4;
  .gameWorksBottomBtn {
    display: flex;
    align-items: center;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    justify-content: space-between;
    box-shadow: 0px -5px 16px 0px rgba(0, 0, 0, 0.04);
    z-index: 2;
    .btn {
      font-size: 16px;
      color: #fe741d;
      flex: 1;
      padding: 16px 0;
      text-align: center;
      background: #ffffff;
    }
    .change {
      background: #fe741d;
      color: #ffffff;
    }
  }
}
</style>



