<template>
  <div class="videoComp">
    <!-- v-if="videoInfo.media_url" -->
    <video id="videoElement" :src='videoInfo.media_url' controls width="100%" ref="video" @ended='onPlayerEnded'></video>
    <div :class="hideImg" :style="`background-image: url('${videoInfo.cover_url}')`">
      <img class="play" @click="playVideo" src="../../assets/img/play-btn.png" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'mxVideo',
  data () {
    return {
      videoLoading: false,
      // 视频实例
      videoInstance: null,
      hide: false,
      playerOptions: {
        // playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: false, // 如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: 'zh-CN',
        aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            type: 'video/mp4', // 这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
            src: 'this.newVideoInfo.media_url' // url地址
          }
        ],
        hls: true,
        poster: 'this.newVideoInfo.cover_url', // 你的封面地址
        width: document.documentElement.clientWidth, // 播放器宽度
        notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true // 全屏按钮
        }
      }
    }
  },
  computed: {
    ...mapState({
      videoInfo: state => state.admin.gameIntroduction.videoInfo
    }),
    hideImg () {
      return this.hide ? 'hide' : 'poster'
    },
    ...mapState({
      videoInfo: state => state.admin.gameIntroduction.videoInfo
    })
  },
  props: {
    videoData: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  methods: {
    // 播放结束
    onPlayerEnded (player) {
      // console.log('player ended!', player)
      this.hide = false
    },
    playVideo () {
      this.hide = !this.hide
      this.videoInstance && this.videoInstance.play()
    }
  },
  mounted () {
    const player = this.$refs.video
    this.videoInstance = player
    // eslint-disable-next-line no-undef
    // if (flvjs.isSupported()) {
    //   // url: this.newVideoInfo.media_url
    //   // eslint-disable-next-line no-undef
    //   var flvPlayer = flvjs.createPlayer({
    //     type: 'mp4',
    //     src: 'http://61.128.151.155/vcloud1049.tc.qq.com/1049_M0116300004IrhRU46jS001001682621.f9844.mp4?vkey=F108415CB2679A010BC2268E089834F6C8893D1B9C3F27AC23B423188FC8354CD9F8FDA31A4C34EC0D6EFCC8E7ED26ECB8CC1F34FD93527502794EE228D862CE3EC7A985950870C8968A054F1EDDB7FE750DE6FC1D325770'
    //   })
    //   flvPlayer.attachMediaElement(player)
    //   flvPlayer.load() // 加载
    // }
  }
}
</script>

<style lang="less" scoped>
.videoComp {
  width: 100%;
  position: relative;
  .poster {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: url("../../assets/img/banner.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    .play {
      width: 60px;
      height: 60px;
    }
  }
}
.hide {
  display: none;
}
#videoElement {
  display: block;
}
</style>



